<template>
  <div class="wrapper">
    <!-- 公共头 -->
    <v-head></v-head>
    <!-- 左侧菜单 -->
    <side-menu></side-menu>
    <div :class="isCollapse==false?'content':'contents'">
      <!-- 路由动画载体 -->
      <transition :name="transitionName">
      <!-- 路由视图 -->
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
  import vHead from '../components/Header.vue';
  import sideMenu from '../components/Menu.vue';
  import EventHandler from '../common/js/EventHandler.js';
  export default {
    data() {
      return {
        isCollapse: false,
        transitionName:'',//路由切换动画
        menuList: [
            {
                "code":"basic_info",
                "id":1,
                "name":"基础信息",
                "parentId":0,
                "permissionClass":"iconfont icon-dazhong-6",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"dealer_maintenance",
                        "id":2,
                        "name":"经销商维护",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/maintenance"
                    },
                    {
                        "butPermList":[
                            {
                                "code":"manage",
                                "id":22,
                                "name":"管理"
                            },
                            {
                                "code":"query",
                                "id":23,
                                "name":"查询"
                            }
                        ],
                        "code":"questionaire_query",
                        "id":3,
                        "name":"问卷查询",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/investigate"
                    }
                ],
                "targetPage":""
            },
            {
                "code":"mission_manage",
                "id":4,
                "name":"任务管理",
                "parentId":0,
                "permissionClass":"iconfont icon-dazhong-1",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"mission_maintenance",
                        "id":5,
                        "name":"任务维护",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/taskManage"
                    },
                    {
                        "butPermList":null,
                        "code":"mission_sche_maintenance",
                        "id":6,
                        "name":"任务行程管理",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/taskScheManage"
                    },
                    {
                        "butPermList":null,
                        "code":"mission_approve_first",
                        "id":7,
                        "name":"任务审核",
                        "permissionKey":"初审",
                        "permissionType":1,
                        "targetPage":"/taskExamine"
                    },
                    {
                        "butPermList":null,
                        "code":"mission_status_count",
                        "id":8,
                        "name":"任务状态统计",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/taskStatusStatic"
                    }
                ],
                "targetPage":""
            },
            {
                "code":"report",
                "id":9,
                "name":"报表",
                "parentId":0,
                "permissionClass":"iconfont icon-dazhong-4",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"query_score",
                        "id":10,
                        "name":"得分查看",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/scoreView"
                    },
                    {
                        "butPermList":null,
                        "code":"count_score",
                        "id":11,
                        "name":"得分统计",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/scoreStatist"
                    },
                    {
                        "butPermList":null,
                        "code":"sche_status_count",
                        "id":12,
                        "name":"行程状态统计",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/travelState"
                    }
                ],
                "targetPage":""
            },
            {
                "code":"complain",
                "id":13,
                "name":"申诉",
                "parentId":0,
                "permissionClass":"iconfont icon-dazhong-3",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"comp_approve_platform",
                        "id":14,
                        "name":"申诉审核",
                        "permissionKey":"平台审",
                        "permissionType":1,
                        "targetPage":"/appealReview"
                    },
                    {
                        "butPermList":null,
                        "code":"comp_deadline_set",
                        "id":15,
                        "name":"申诉期限设置",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/termSetting"
                    }
                ],
                "targetPage":""
            }
        ], //菜单列表

      }
    },
    created() {

    },
    methods: {

    },
    mounted(){
      var self = this;
      // 组件传值 接收
      EventHandler.$on( "myMenuEv", function(data){
        self.isCollapse = data;
      } );
    },
    components: {
      vHead,
      sideMenu
    },
    watch: {//使用watch 监听$router的变化
      $route(to, from) {
        //如果to索引大于from索引,判断为前进状态,反之则为后退状态
        if(to.meta.index > from.meta.index){
          //设置动画名称
          this.transitionName = 'slide-left';
        }else{
          this.transitionName = 'slide-right';
        }
      }
    }

  }
</script>

<style>
  .wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  /*content样式*/

  .content {
    background: none repeat scroll 0 0 #F3F3F5;
    position: absolute;
    left: 239px;
    right: 0;
    top: 70px;
    bottom: 0;
    width: auto;
    overflow-x: hidden;
    padding: 15px;
  }

  .contents {
    background: none repeat scroll 0 0 #F3F3F5;
    position: absolute;
    left: 64px;
    right: 0;
    top: 70px;
    bottom: 0;
    width: auto;
    padding: 15px;
    overflow-x: hidden;
  }
  /*菜单展开收起样式*/

  .el-radio__inner {
    height: 0;
    width: 0;
    border: 0;
  }

  .el-radio__inner::after {
    height: 0;
    width: 0;
  }

  .el-radio__input.is-checked .el-radio__inner {
    border-color: #FFFFFF;
    background: #FFFFFF;
  }

  .el-radio {
    color: #FFFFFF;
    font-weight: 500;
    line-height: 1;
    cursor: pointer;
    white-space: nowrap;
    outline: 0;
  }

  .el-radio__input.is-checked+.el-radio__label {
    color: #FFFFFF;
  }

  .el-radio+.el-radio {
    margin-left: 0px;
  }


  .el-icon-arrow-down:before {
    content: ''
  }

  .el-submenu__title i {
    color: #FFFFFF;
  }

  .el-menu--collapse .el-submenu .el-menu {
    border: none;
  }

  .el-dropdown {
    color: #FFFFFF;
    /* color: #606266; */
    font-size: 18px;
  }
    /*路由动画*/
  .slide-right-enter-active,
  .slide-right-leave-active,
  .slide-left-enter-active,
  .slide-left-leave-active {
    will-change: transform;
    transition: all 600ms;
    position: absolute;
  }
  .slide-right-enter {
    opacity: 0;
    /*transform: translate3d(-100%, 0, 0);*/
  }
  .slide-right-leave-active {
    opacity: 0;
    /*transform: translate3d(100%, 0, 0);*/
  }
  .slide-left-enter {
    opacity: 1;
    /*transform: translate3d(100%, 0, 0);*/
  }
  .slide-left-leave-active {
    opacity: 1;
    /*transform: translate3d(-100%, 0, 0);*/
  }
</style>



