<template>
    <!--头部-->
    <div :class="isCollapse==true?'header':'headers'">
      <div style="width: 100%;display: flex;" class="headerBg">
        <div class="logo">
          <div class="toggleBtn" @click="sendMsg">
            <i class="el-icon-tickets"></i>
<!--             <el-radio-group v-model="isCollapse">
              <el-radio :label="false" v-show="isCollapse==true"><i class="iconfont icon-dazhong-2"></i>true</el-radio>
              <el-radio :label="true" v-show="isCollapse==false"><i class="iconfont icon-dazhong-2"></i>false</el-radio>
            </el-radio-group> -->
          </div>

        </div>

        <div :class="isCollapse==false?'user-info':'user-infos'" >
          <span class="iconfont icon-dazhong-19" style="font-size: 28px;"></span>
          <span style="display: inline-block;word-break: break-all;" @click='quit'>退出登录</span>


        </div>
      </div>
    </div>

</template>

<script>
  import EventHandler from '../common/js/EventHandler.js';// 用于给同级组件之间传递事件

  export default {
    data() {
      return {
        isCollapse: true,
      }
    },
    created() {


    },
    methods: {
      //退出
      quit() {
        var self = this;
        self.$confirm("确认退出吗？", '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          closeOnClickModal: false,
          type: 'warning'
        }).then(() => {
          sessionStorage.clear();
          localStorage.clear();
          self.$router.push('/login');
        }).catch(() => {
          // self.$message({
          //   type: 'info',
          //   message: '已取消操作',
          //   duration: 4500
          // });
        });
      },
      sendMsg(){
        // 组件传值 发送
        EventHandler.$emit('myMenuEv',this.isCollapse);
        this.isCollapse=!this.isCollapse;
      }
    }
  }
</script>

<style>
  /*头部css*/

  .headerBg {
    background: #1B87F2;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }


  .header {
    position: relative;
    /*box-sizing: border-box;*/
    /*left: 239px;*/
    height: 70px;
    font-size: 22px;
    line-height: 70px;
    color: #fff;
    /*padding-left: 20px;*/
    display: flex;
    z-index: 1;
    margin-left: 239px;
  }

  .headers {
    position: relative;
    height: 70px;
    font-size: 22px;
    line-height: 70px;
    color: #fff;
    background: #1B87F2;
    display: flex;
    z-index: 1;
    margin-left: 64px;
  }

  .logo {
    /*margin-left: 30px;*/
    height: 70px;
    text-align: center;
    width: 60px;
    cursor: pointer;
  }

  .brand {
    flex: 1;
  }

  .el-dropdown-link:hover{
    cursor:pointer;
  }
  .user-info {
    /*width: 500px;*/
    font-size: 16px;
    color: #fff;
    /*line-height: 70px;*/
    display: flex;
    padding-right: 20px;
    /*-webkit-box-align: center;*/

  }

  .user-infos {
    /*width: 300px;*/
    font-size: 16px;
    color: #fff;
    line-height: 70px;
    display: flex;
    padding-right: 20px;
    -webkit-box-align: center;

  }


</style>



