<template>
    <div :class="isCollapse==false?'menu':'menus'">
      <!-- logo  图片 -->
      <div class="logoView">
        <img src="../common/img/icon_logo_login.png" class="logoBig" alt="" v-show="!isCollapse">
        <img src="../common/img/icon_logos.png" class="logoSmall" width="56px" alt="" v-show="isCollapse">
      </div>
      <!-- 左侧菜单 -->
      <div class="menuView" :style="fullHeight">

        <el-menu :default-active="$route.path" :collapse="isCollapse" :router="true" class="el-menu-vertical-demo" background-color="#343A55" text-color="#fff" active-text-color="#20a0ff">
          <el-submenu v-for="(menu,index) in menuList" :key="index" :index="menu.id | changetostr">
            <template slot="title" style="background: rgb(237, 246, 254);">
              <i :class="menu.permissionClass"></i>
              <span>{{menu.name}}</span>
            </template>
            <el-menu-item-group>
              <el-menu-item v-for="child in menu.subPermissionList" :key="child.targetPage" :index="child.targetPage" style="padding-left: 55px;">{{child.name}}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </div>

    </div>
</template>

<script>
  import EventHandler from '../common/js/EventHandler.js';
  export default {
    data() {
      return {
        isCollapse: false,
        fullHeight:{// 动态获取屏幕高度
          height: (document.documentElement.clientHeight-70)+"px"
        },
        menuList: [
            {
                "code":"basic_info",
                "id":1,
                "name":"基础信息",
                "parentId":0,
                "permissionClass":"el-icon-location",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"dealer_maintenance",
                        "id":2,
                        "name":"经销商维护",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/maintenance"
                    },
                    {
                        "butPermList":[
                            {
                                "code":"manage",
                                "id":22,
                                "name":"管理"
                            },
                            {
                                "code":"query",
                                "id":23,
                                "name":"查询"
                            }
                        ],
                        "code":"questionaire_query",
                        "id":3,
                        "name":"问卷查询",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/investigate"
                    }
                ],
                "targetPage":""
            },
            {
                "code":"mission_manage",
                "id":4,
                "name":"任务管理",
                "parentId":0,
                "permissionClass":"el-icon-menu",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"mission_maintenance",
                        "id":5,
                        "name":"任务维护",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/taskManage"
                    },
                    {
                        "butPermList":null,
                        "code":"mission_sche_maintenance",
                        "id":6,
                        "name":"任务行程管理",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/taskScheManage"
                    },
                    {
                        "butPermList":null,
                        "code":"mission_approve_first",
                        "id":7,
                        "name":"任务审核",
                        "permissionKey":"初审",
                        "permissionType":1,
                        "targetPage":"/taskExamine"
                    },
                    {
                        "butPermList":null,
                        "code":"mission_status_count",
                        "id":8,
                        "name":"任务状态统计",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/taskStatusStatic"
                    }
                ],
                "targetPage":""
            },
            {
                "code":"report",
                "id":9,
                "name":"报表",
                "parentId":0,
                "permissionClass":"el-icon-document",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"query_score",
                        "id":10,
                        "name":"得分查看",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/scoreView"
                    },
                    {
                        "butPermList":null,
                        "code":"count_score",
                        "id":11,
                        "name":"得分统计",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/scoreStatist"
                    },
                    {
                        "butPermList":null,
                        "code":"sche_status_count",
                        "id":12,
                        "name":"行程状态统计",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/travelState"
                    }
                ],
                "targetPage":""
            },
            {
                "code":"complain",
                "id":13,
                "name":"申诉",
                "parentId":0,
                "permissionClass":"el-icon-setting",
                "permissionKey":"",
                "permissionType":1,
                "subPermissionList":[
                    {
                        "butPermList":null,
                        "code":"comp_approve_platform",
                        "id":14,
                        "name":"申诉审核",
                        "permissionKey":"平台审",
                        "permissionType":1,
                        "targetPage":"/appealReview"
                    },
                    {
                        "butPermList":null,
                        "code":"comp_deadline_set",
                        "id":15,
                        "name":"申诉期限设置",
                        "permissionKey":"",
                        "permissionType":1,
                        "targetPage":"/termSetting"
                    }
                ],
                "targetPage":""
            }
        ], //菜单列表
      }
    },
    created() {
      console.log(this.fullHeight)

    },
    mounted(){
      let self = this;
      EventHandler.$on( "myMenuEv", function(data){
        self.isCollapse = data;
      } );

    },
    methods: {

    },
    watch: {//使用watch 监听$router的变化
      $route(to, from) {

      }
    }
  }
</script>

<style>
  /*menu的css*/
  .logoView{
    height: 70px;
    width: 100%;
  }
  .logoView .logoBig{
    margin:11px 19px;
  }
  .logoView .logoSmall{
    margin:14px 4px;
  }
  .menuView{
    overflow-y: auto;
  }
  .menu {
    /*display: block;*/
    z-index: 99;
    position: absolute;
    width: 245px;
    left: 0;
    top: 0px;
    bottom: 0;
    /*background-image: linear-gradient(0deg, #24283E 0%, #485173 100%);*/
    background: #343A55;
  }

  .menus{
    /*display: block;*/
    z-index: 99;
    position: absolute;
    /*width: 239px;*/
    left: 0;
    top: 0px;
    bottom: 0;
    /*background-image: linear-gradient(0deg, #24283E 0%, #485173 100%);*/
    background: #343A55;
  }

  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 240px;
    /*min-height: 400px;*/
  }

  .el-col-12 {
    width: 240px;
  }

  .el-submenu__title i {
    /*color: #02b9e3;*/
    font-size: 28px;
  }

  .el-submenu__title span {
    margin-left: 15px;
  }

  .el-menu-item.is-active {
    background: rgb(237, 246, 254);
    color: #333;
  }

  .el-menu-item:active {
    background: rgb(237, 246, 254);
    color: #333;
  }

  .el-menu-item-group__title {
    padding: 0px;
  }

  .el-submenu__title {
    /*background: linear-gradient(90deg, #485173 0%, #24283E 100%);*/
  }

  .el-submenu__title:hover {
    color: #FFFFFF;
    border-left: 2px solid #1393F3;
    /*background-image: linear-gradient(90deg, #485173 0%, #24283E 100%);*/
  }

  .el-submenu__title:active {
    border-left: 2px solid #1393F3;
    /*background-image: linear-gradient(0deg, #485173 0%, #24283E 100%);*/
  }

  .el-menu {
    border-right: none;
  }
  /*菜单展开收起样式*/

  .el-radio__inner {
    height: 0;
    width: 0;
    border: 0;
  }

  .el-radio__inner::after {
    height: 0;
    width: 0;
  }

  .el-radio__input.is-checked .el-radio__inner {
    border-color: #FFFFFF;
    background: #FFFFFF;
  }

  .el-radio {
    color: #FFFFFF;
    font-weight: 500;
    line-height: 1;
    cursor: pointer;
    white-space: nowrap;
    outline: 0;
  }

  .el-radio__input.is-checked+.el-radio__label {
    color: #FFFFFF;
  }

  .el-radio+.el-radio {
    margin-left: 0px;
  }
  /*大众和斯柯达切换*/

  .brand {
    padding-left: 20%;
  }
  .brand:hover{
    cursor:pointer;
  }
  .brand ul {
    width: 100%;
  }

  .brand li {
    float: left;
    font-size: 16px;
  }

  li {
    list-style: none;
  }

  .active {
    opacity: 0.5;
  }

  .el-icon-arrow-down:before {
    content: ''
  }

  .el-submenu__title i {
    color: #FFFFFF;
  }

  .el-menu--collapse .el-submenu .el-menu {
    border: none;
  }

  .el-dropdown {
    color: #FFFFFF;
    /* color: #606266; */
    font-size: 18px;
  }

</style>



